<template>
    <template v-if="data.ready">
        <div class="head">
            <Menu :items="data.sections"
                :code="data.section?.code"
                :useI18n="false"
                @change="setSection"
            />
        </div>

        <ShowcaseSection v-if="data.section?.id"
            :sectionId="data.section?.id"
        />

        <MoreDiscoveries />

    </template>

    <Ready page="music" :init="init" />
</template>

<script setup>
    import { computed, reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import ShowcaseSection from './components/ShowcaseSection.vue';
    import MoreDiscoveries from './components/MoreDiscoveries.vue';
    import Menu from 'ðŸ”—/components/ui/Menu.vue';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';

    const { t } = useI18n();

    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    const showcase = api.showcase();
    const user = stores.user();

    const others = stores.others();

    const data = reactive({
        ready: false,
        sections: [],
        section: null,
    });

    const setSection = (code) => {
        data.section = data.sections.find(item => item.code === code);
    }

    const init = async () => {
        // Ð¡ÐµÐºÑ†Ð¸Ð¸
        let payload = await showcase.section.list({
            showed: true,
            page: 'MUSIC'
        });

        data.sections = payload?.items || [];
        data.section = data.sections[0]; 

        console.log(data.sections);

        data.ready = true;
    }

    defineOptions({
        name: 'music'
    });

    // onBeforeUnmount(() => {
    // });
</script>

<style lang="scss" scoped>
    .head {
        @include page-title;
        ul {
            margin: 0 auto;
        }
    }

    .empty-block {
        display: flex;
        flex-direction: column;
        gap: 8px;
        align-items: center;
        justify-content: center;
        margin: auto;

        .empty {
            margin: 0;
        }
    }
    .empty-logo {
        position: relative;
        animation: jump 3s infinite;
        width: 133px;
        flex-shrink: 0;


        @keyframes jump {
            0%, 20%, 50%, 80%, 100% {
                transform: translateY(0);
            }
            40% {
                transform: translateY(-6px);
            }
            60% {
                transform: translateY(-3px);
            }
        }

        img {
            height: 200px;
            width: 100%;
            transition: transform 0.5s ease, opacity 0.5s ease;
            &.loading {
                transform: scale(0);
                opacity: 0;
            }
        }

        img:nth-child(1) {
            filter: drop-shadow(0 0 4px var(--color--option-5));
        }

        img:nth-child(2) {
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%); 
            filter: invert(1) brightness(4334); //brightness(9999);


            -webkit-mask-size: 100%;
            -webkit-mask-repeat: no-repeat;
            -webkit-mask-position: left top, left bottom;
            -webkit-mask-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
            @include sunshine(3s, 135deg, 0);
        }
    }
</style>
