<template>
    <template v-if="props.item?.id && props.item?.playlists?.length">
        <template v-if="!props.item.template || props.item.template === 'ROW_BIG'">
            <Playlists
                :items="props.item.playlists.map(item => item.playlist)"
                :loadMore="false"
                :light="false"
                type="row"
                :title="{ text: props.item.name }"
                handler="playlist"
            />
        </template>

        <template v-else-if="props.item.template === 'ROW'">
            <Playlists
                :items="props.item.playlists.map(item => item.playlist)"
                :loadMore="false"
                :light="true"
                type="row"
                :title="{ text: props.item.name }"
                handler="playlist"
            />
            <!-- <Playlists
                :items="props.item.playlists.map(item => item.playlist)"
                type="row"
                :source="null"
                :loadMore="false"
                handler="playlist"
                :title="{ text: props.item.name }"
            /> -->
        </template>

        <template v-else-if="props.item.template === 'LIST'">
            <Playlists
                :items="props.item.playlists.map(item => item.playlist)"
                :loadMore="false"
                :light="true"
                :showKebab="true"
                :title="{ text: props.item.name }"
                handler="playlist"
            />
        </template>
    </template>
</template>

