<template>
    <template v-if="props.item?.id && props.item?.releases?.length">
        <template v-if="!props.item.template || props.item.template === 'ROW_BIG'">
            <Releases
                :items="props.item.releases.map(item => item.release)"
                :loadMore="false"
                :light="false"
                type="row"
                :title="{ text: props.item.name }"
                handler="release"
            />
        </template>

        <template v-else-if="props.item.template === 'ROW'">
            <Releases
                :items="props.item.releases.map(item => item.release)"
                :loadMore="false"
                :light="true"
                subtype="row"
                type="row"
                :title="{ text: props.item.name }"
                handler="release"
            />
        </template>

        <template v-else-if="props.item.template === 'LIST'">
            <Releases
                :items="props.item.releases.map(item => item.release)"
                :loadMore="false"
                :light="true"
                :showKebab="true"
                :title="{ text: props.item.name }"
                handler="release"
            />
        </template>
    </template>
</template>

<script setup>
    import Releases from 'ðŸ”—/components/blocks/Releases.vue';

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });
</script>

