<template>
    <template v-if="props.item?.id && props.item?.artists?.length">
        <template v-if="!props.item.template || props.item.template === 'ROW_BIG'">
            <Artists
                :items="props.item.artists.map(item => item.artist)"
                type="row"
                :source="null"
                :loadMore="false"
                handler="artist"
                :title="{ text: props.item.name }"
            />
        </template>
        <template v-else-if="props.item.template === 'ROW'">
            <Artists
                :items="props.item.artists.map(item => item.artist)"
                type="row"
                subtype="row"
                :itemsInCol="2"
                :source="null"
                :loadMore="false"
                handler="artist"
                :title="{ text: props.item.name }"
            />
        </template>
        <template v-else-if="props.item.template === 'LIST'">
            <Artists
                :items="props.item.artists.map(item => item.artist)"
                type="list"
                :source="null"
                :loadMore="false"
                :showKebab="true"
                handler="artist"
                :title="{ text: props.item.name }"
            />
        </template>
    </template>
</template>

<script setup>
