<template>
    <template v-if="props.item?.id && props.item?.tracks?.length">
        <template v-if="!props.item.template || props.item.template === 'ROW_BIG'">
        </template>

        <template v-else-if="props.item.template === 'ROW'">
            <Tracks
                :items="props.item.tracks.map(item => item.track)"
                type="row"
                :source="null"
                :loadMore="false"
                handler="track"
                :title="{ text: props.item.name }"
            />
        </template>

        <template v-else-if="props.item.template === 'LIST'">
            <Tracks
                :items="props.item.tracks.map(item => item.track)"
                type="list"
                :source="null"
                :loadMore="false"
                :showKebab="true"
                handler="track"
                :title="{ text: props.item.name }"
            />
        </template>
    </template>
</template>

<script setup>
    import Tracks from 'ðŸ”—/components/blocks/Tracks.vue';

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });
</script>

