<template>
    <template v-for="item in data.widgets" :key="item.id">

        <template v-if="item.type === 'EDITORIAL'">
            <WidgetArtists v-if="item.artists?.length" :item="item" />
            <WidgetTracks v-else-if="item.tracks?.length" :item="item" />
            <WidgetReleases v-else-if="item.releases?.length" :item="item" />
            <WidgetPlaylists v-else-if="item.playlists?.length" :item="item" />
        </template>
    </template>
</template>

<script setup>
    import { reactive, watch, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';

    import WidgetArtists from './widgets/WidgetArtists.vue';
    import WidgetTracks from './widgets/WidgetTracks.vue';
    import WidgetReleases from './widgets/WidgetReleases.vue';
    import WidgetPlaylists from './widgets/WidgetPlaylists.vue';

    import stores from '@/stores';
    import api from 'ðŸ”—/api';


    const { t } = useI18n();

    const showcase = api.showcase();

    const props = defineProps({
        sectionId: { type: [String, null], default: null },
        parentId: { type: [String, null], default: null }
    });

    const data = reactive({
        widgets: []
    });

    const getWidgets = async () => {
        const payload = await showcase.widget.list({
            sectionId: props.sectionId,
            parentId: props.parentId
        });

        data.widgets = payload?.items || [];
        console.log(payload)
    }




    watch(
        () => props.sectionId,
        () => {
            getWidgets();
        }
    );

    watch(
        () => props.parentId,
        () => {
            getWidgets();
        }
    );

    onMounted(() => {
        getWidgets();
    });
</script>

